<template>
  <div class="palette-demo palette-demo-tab-picker cleafix">
    <md-field title="å°å">
      <md-field-item
        title="èç³»å°å"
        arrow="arrow-right"
        @click="show = !show"
        placeholder="è¯·éæ©èç³»å°å"
        :content="addressStr"
        solid
      />
    </md-field>
    <md-tab-picker
      title="è¯·éæ©"
      describe="è¯·éæ©æ¨æå¨ççä»½ãåå¸ãåºå¿"
      :data="data"
      v-model="show"
      @change="handleChange"
    />
  </div>
</template>

<script>
import data from 'mand-mobile/components/tab-picker/demo/data'

export default {
  data () {
    return {
      show: true,
      address: [],
      data,
    }
  },
  computed: {
    addressStr() {
      return this.address.map(item => item.label).join(' ')
    },
  },
  methods: {
    handleChange({options}) {
      this.address = options
    },
  },
}
</script>

<style lang="stylus">
.palette-demo-tab-picker
  .md-tab-picker .md-popup
    position absolute
</style>



